import { useState } from "react";
import { IProject } from "../../../models/IProjects";

interface IProps {
  currentProject?: IProject;
  onSave: (project: IProject) => void;
}

const emptyProject: IProject = {
  id: "",
  name: "",
  description: "",
  repositories: [],
  monitoring: [],
};

const ProjectForm = ({ currentProject, onSave }: IProps) => {
  const [project, set_project] = useState(currentProject ?? emptyProject);

  const handleSave = () => {
    if (onSave) {
      onSave(project);
    }
  };

  const handleChange = (e: any) => {
    set_project({
      ...project,
      [e.target.name]: e.target.value,
    });
  };

  return (
    <div
      className="modal fade"
      id="projectForm"
      tabIndex={-1}
      aria-labelledby="projectFormLabel"
      aria-hidden="true"
    >
      <div className="modal-dialog modal-fullscreen">
        <div className="modal-content">
          <div className="modal-header">
            <h1 className="modal-title fs-5" id="projectFormLabel">
              Aplicao
            </h1>
          </div>
          <div className="modal-body">
            <div className="mb-3">
              <label htmlFor="id" className="form-label">
                ID
              </label>
              <input
                type="text"
                name="id"
                className="form-control"
                id="id"
                value={project.id}
                onChange={handleChange}
              />
            </div>
          </div>
          <div className="modal-footer">
          <button
              type="button"
              className="btn btn-secondary"
              data-bs-dismiss="modal"
            >
              Cancelar
            </button>  <button
              type="button"
              className="btn btn-primary"
              data-bs-dismiss="modal"
              onClick={handleSave}
            >
              Salvar
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProjectForm;
